// Images
import fish from "../../img/fish.jpeg";
import nonePage from "../../img/shitpost.png";
import { actionCreator } from "../actionCreator";

let initalState = {
  location: {
    //Public location config
    home: "dooboox/",
    content: "dooboox/content",
    authors: "dooboox/authors",
    about: "dooboox/about",
  },

  images: {
    fish,
    nonePage,
    logo: "https://upload.wikimedia.org/wikipedia/commons/a/a7/React-icon.svg",
  },

  minimap: {
    mode: "minimap", // or "minimap minimap_active"
    curtainHeight: document.body.clientHeight + "px",
    curtainTopOffset: "0%",
    buttonClassList: "minimapButton",

    onScroll: () => {
      let winScroll = document.body.scrollTop || document.documentElement.scrollTop;
      let height = document.documentElement.scrollHeight - document.documentElement.clientHeight;
      let scrolled = (winScroll / height) * 100;
      return scrolled + "%";
    }
  },

  bodyScrollState: "UNLOCKED", // Lock/unlock body scrollbar
  mobileScreenWidth: 590, // Mobile screen resolution (px)

  actionCreator, // Create an action and dispatch it
};

export const rootReducer = (state = initalState, action) => {
  switch (action.type) {
    case "SWITCH_BODY_SCROLL": {
      switch (action.payload) {
        case "LOCK": {
          // Body scrollbar lock/unlock action api
          document.getElementsByTagName("body")[0].classList.add("scroll-lock");
          return { ...state, bodyScrollState: "LOCKED" };
        }

        case "UNLOCK": {
          // yep-yep
          document
            .getElementsByTagName("body")[0]
            .classList.remove("scroll-lock");
          return { ...state, bodyScrollState: "UNLOCKED" };
        }
      }
    }

    case "SWITCH_MINIMAP_MODE": {
      return { ...state, minimap: { ...state.minimap, mode: action.payload.minimapClassList, buttonClassList: action.payload.buttonClassList }}
    }

    case "SCROLL_MINIMAP": {
      return { ...state, minimap: { ...state.minimap, curtainTopOffset: action.payload } }
    }
  }

  return state;
};
